import {Injectable} from '@angular/core';
import {FollowingClass} from '../models/following-class';

import {HttpClient} from '@angular/common/http';
import {Observable, of} from 'rxjs';
import {catchError, tap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class FollowingClassService {
  private followingClassURL = 'http://5c6931b6bad2070014abb4d3.mockapi.io/courses';

  getFollowingClass(): Observable<FollowingClass[]> {
    return this.http.get<FollowingClass[]>(this.followingClassURL).pipe(
      tap(receiveFollowingClass => JSON.stringify(receiveFollowingClass)),
      catchError(error => of([]))
    );
  }
  getNameFollowingClass(id): Observable<FollowingClass[]> {
    return this.http.get<FollowingClass[]>(`${this.followingClassURL}/${id}`).pipe(
      tap(receiveFollowingClass => JSON.stringify(receiveFollowingClass)),
      catchError(error => of([]))
    );
  }

  constructor(
    private http: HttpClient) {
  }
}
