import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {AuthGuard} from './auth/auth.guard';
import {SecureComponent} from './secure/secure.component';
import {PublicComponent} from './public/public.component';
import {PUBLIC_ROUTES} from './public/public_routes';
import {SECURE_ROUTES} from './secure/secure_routes';
import {NotFoundComponent} from './not-found/not-found.component';

let param = 'home';
if ( localStorage.getItem('data') === null) {
  param = 'home';
} else {
  param = 'user';
}
export let APP_ROUTES: Routes = [
  { path: '', redirectTo: `${param}`, pathMatch: 'full', },
  { path: 'home', redirectTo: `${param}`, pathMatch: 'full', },
  { path: '', component: PublicComponent, data: { title: 'Public Views' }, children: PUBLIC_ROUTES },
  { path: '', component: SecureComponent, canActivate: [AuthGuard], data: { title: 'Secure Views' }, children: SECURE_ROUTES },
  { path: '**', component: NotFoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(APP_ROUTES)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
