import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable, of} from 'rxjs';
import {Content} from '../models/content';
import {catchError, tap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ContentsService {

  constructor(private http: HttpClient) {
  }

  private coursesURL = 'http://5c6931b6bad2070014abb4d3.mockapi.io/courses';

  getContents(id1: number): Observable<Content[]> {
    return this.http.get<Content[]>(`${this.coursesURL}/${id1}/contents`).pipe(
      tap(receiveContents => JSON.stringify(receiveContents)),
      catchError(error => of([]))
    );
  }
}
