import {Component, OnInit} from '@angular/core';
import {ContentsService} from '../../../../services/contents.service';
import {Content} from '../../../../models/content';
import {ActivatedRoute} from '@angular/router';
import {PopupFormContentComponent} from './popup-form-content/popup-form-content.component';
import {MatDialog} from '@angular/material';
import {PopupContentDetailComponent} from './popup-content-detail/popup-content-detail.component';
import {PopupTakeAttendantComponent} from './popup-take-attendant/popup-take-attendant.component';


@Component({
  selector: 'app-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.styl']
})
export class ContentComponent implements OnInit {
  contents: Content[];
  isPopupOpened = true;
  content = '';
  constructor(
    private contentService: ContentsService,
    private route: ActivatedRoute,
    private dialog?: MatDialog
  ) {
  }

  getContents(): void {
    const id = +this.route.snapshot.paramMap.get('id');
    this.contentService.getContents(id).subscribe(
      (receiveContents) => {
        this.contents = receiveContents;
      }
    );
  }

  addContent() {
    this.isPopupOpened = true;
    const dialogRef = this.dialog.open(PopupFormContentComponent, {
      data: {}
    });


    dialogRef.afterClosed().subscribe(result => {
      this.isPopupOpened = false;
    });
  }

  showAttendace() {
    this.isPopupOpened = true;
    const dialogRef = this.dialog.open(PopupTakeAttendantComponent, {
      data: {}
    });


    dialogRef.afterClosed().subscribe(result => {
      this.isPopupOpened = false;
    });
  }

  showContentDetail() {
    this.isPopupOpened = true;
    const dialogRef = this.dialog.open(PopupContentDetailComponent, {
      data: {}
    });


    dialogRef.afterClosed().subscribe(result => {
      this.isPopupOpened = false;
    });
  }

  ngOnInit() {
    this.getContents();
  }

}
