import { Injectable } from '@angular/core';
import {Observable, of} from 'rxjs';
import {Users} from '../models/users';
import {catchError, tap} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';
import {Members} from '../models/member';

@Injectable({
  providedIn: 'root'
})
export class AddMemberService {
  getUsers(usersURL): Observable<Users[]> {
    return this.http.get<Users[]>(usersURL).pipe(
      tap( receiveCourses => JSON.stringify(receiveCourses)),
      catchError(error => of([]))
    );
  }
  addMember(newMember: Members, memberURL): Observable<Members> {
    return this.http.post<Members>(memberURL, newMember)
      .pipe(
        tap((member: Members) => console.log(JSON.stringify(member))),
        catchError(error => of(new Members()))
      );
  }
  constructor(private http: HttpClient) { }
}
