import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.styl']
})
export class ProfileComponent implements OnInit {
  username = '';
  imageUrl = '';
  email = '';
  constructor() { }
  public getData() {
    const dataUserLogin = JSON.parse(localStorage.getItem('data'));
    if (dataUserLogin != null) {
      this.username = dataUserLogin.name;
      this.imageUrl = dataUserLogin.image;
      this.email = dataUserLogin.email;
    }
  }
  ngOnInit() {
    this.getData();
  }

}
