import { Injectable } from '@angular/core';
import {Observable, of} from 'rxjs';
import {catchError, tap} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';
import {Member} from '../models/mock-member';

@Injectable({
  providedIn: 'root'
})
export class MembersService {
  private coursesURL = 'http://5c6931b6bad2070014abb4d3.mockapi.io/courses';
  getMembers(id1: number): Observable<Member[]> {
    return this.http.get<Member[]>(`${this.coursesURL}/${id1}/members?sortBy=userName&order=asc`).pipe(
      tap(receiveMembers => JSON.stringify(receiveMembers)),
      catchError(error => of([]))
    );
  }
  constructor(private http: HttpClient) { }
}
