import { Injectable } from '@angular/core';

import { Observable, of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {Message} from '../models/message';

@Injectable({
  providedIn: 'root'
})
export class LogoutService {
  sendMessageLogoutToServer(newMessage: string): Observable<Message> {
    const logoutURL = 'http://192.168.4.42:8080/v1/sessions';
    // const logoutURL = 'http://localhost:3000';
    // @ts-ignore
    return this.http.delete<Message>(logoutURL + '/' + `${localStorage.getItem('tokenServer')}`)
      .pipe(
        tap(_ => {
          console.log('deleted');
          localStorage.clear();
          window.location.reload();
        }
    ),
    catchError(error => of()));
  }
  constructor(
    private http: HttpClient) { }
}
