import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {AddMemberService} from '../../../services/add-member.service';
import {Users} from '../../../models/users';
import {Members} from '../../../models/member';
import {Title} from '@angular/platform-browser';
import {FollowingClassService} from '../../../services/following-class.service';
import {FollowingClass} from '../../../models/following-class';
import {Classes} from '../../../models/class';
import {NotificationAlertService} from '../../../services/notification-alert.service';

@Component({
  selector: 'app-class-detail',
  templateUrl: './class-detail.component.html',
  styleUrls: ['./class-detail.component.styl']
})
export class ClassDetailComponent implements OnInit {
  user: Users[];
  us: Users[];
  // @ts-ignore
  selectedUser: Users = [];
  name = '';
  value = '';
  imageUrl = '';
  nameClass: FollowingClass[];
  class: Classes;
  constructor(
    private addMemberService: AddMemberService,
    private route: ActivatedRoute,
    private router: Router,
    private followingClass: FollowingClassService,
    private titleService: Title,
    private notificationAlertService: NotificationAlertService
  ) {
    this.titleService.setTitle('Kha hc | NTQ Learning Social NetWork');
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
  }
  searchUser(tooltip) {
    const usersURL = 'http://5c473d7a1ef6050014d21e28.mockapi.io/users/1/info?search=';
    const dataUserLogin = JSON.parse(localStorage.getItem('data'));
    this.imageUrl = dataUserLogin.image;
    if ( this.value !== '') {
      this.addMemberService.getUsers(usersURL + this.value).subscribe(
        (updatedUser) => {
          // @ts-ignore
          this.user = updatedUser;
          this.us = updatedUser;
          // @ts-ignore
          if ( JSON.stringify(updatedUser) !== '[]' ) {
            tooltip.open();
          } else {
            this.user = [
              {
                id: '',
                email: '',
                userId: '',
                name: 'No results',
                createdAt: 'https://institutogoldenprana.com.br/wp-content/uploads/2015/08/no-avatar-25359d55aa3c93ab3466622fd2ce712d1.jpg',
              }
            ];
            tooltip.open();
            // alert('No result');
          }
        });
    } else {
      tooltip.close();
    }
  }

  addMember() {
    if ( this.value === '') {
      this.notificationAlertService.open('error', 'Error', 'Email not exist in system');
    } else {
      const obj1 = this.us.find(x => x.email === `${this.value}`);
      const obj2 = this.us.find(x => x.email === this.selectedUser.email);
      localStorage.setItem('obj2', JSON.stringify(obj2));
      if ( obj1 !== undefined ) {
        const memberURL = 'http://5c473d7a1ef6050014d21e28.mockapi.io/users/1/info?search=';
        const newMember: Members = new Members();
        newMember.email = this.selectedUser.email;
        newMember.ClassId = +this.route.snapshot.paramMap.get('id');
        if ( JSON.stringify(this.us) !== '[]' ) {
          this.addMemberService.addMember(newMember, memberURL).subscribe(
            insertedToken => insertedToken);
          this.value = '';
          this.us = [];
          this.notificationAlertService.open('success', 'Success', 'Add member success');
        } else {
          this.notificationAlertService.open('error', 'Error', 'Email not exist in system');
        }
      } else {
        if ( this.value === this.selectedUser.email && obj2 !== undefined ) {
          const memberURL = 'http://5c473d7a1ef6050014d21e28.mockapi.io/users/1/info?search=';
          const newMember: Members = new Members();
          newMember.email = this.selectedUser.email;
          newMember.ClassId = +this.route.snapshot.paramMap.get('id');
          this.value = this.selectedUser.email;
          if ( JSON.stringify(this.us) !== '[]' ) {
            this.addMemberService.addMember(newMember, memberURL).subscribe(
              insertedToken => insertedToken);
            this.value = '';
            this.us = [];
            this.notificationAlertService.open('success', 'Success', 'Add member success');
          } else {
            this.notificationAlertService.open('error', 'Error', 'Email not exist in system');
          }
        } else {
          this.notificationAlertService.open('error', 'Error', 'Email not exist in system');
        }
      }
    }
  }

  onSelectUser(users: Users): void {
    this.selectedUser = users;
    this.value = this.selectedUser.email;
  }

  getClassDetail(): void {
    const id = +this.route.snapshot.paramMap.get('id');
    this.followingClass.getNameFollowingClass(id).subscribe(
      (nameClass) => {
        this.nameClass = nameClass;
      }
    );
  }

  ngOnInit() {
    // this.router.params.subcrible(() => {
    //   parmas['id'];
    // })
    this.getClassDetail();
  }
}
