import {Component, EventEmitter, Input, OnInit, Output, TemplateRef, ViewChild} from '@angular/core';
import {AuthService, GoogleLoginProvider} from 'angular-6-social-login';
import {LoginService} from '../../services/login.service';
import {Router} from '@angular/router';
import {Message} from '../../models/message';
import {LogoutService} from '../../services/logout.service';
import {NotificationAlertService} from '../../services/notification-alert.service';


// @ts-ignore
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.styl']
})
export class LoginComponent implements OnInit {
  @Input() clicked = false;
  @Input() isLogin;
  @Output() click = new EventEmitter();

  constructor(
    private notificationAlertService: NotificationAlertService,
    private socialAuthService: AuthService,
    private loginService: LoginService,
    private logoutService: LogoutService,
    private router: Router,
) {
  }
  message: Message;
  username = '';
  linkimage = '';
  email = '';
  isLoggedIn;

  public socialSignIn(event) {
    let socialPlatformProvider;
    socialPlatformProvider = GoogleLoginProvider.PROVIDER_ID;
    this.socialAuthService.signIn(socialPlatformProvider).then(
      (userData) => {
        localStorage.setItem('googleToken', userData.token);
        this.loginService.getTokenFromServer().subscribe(
          (updatedToken) => {
            // @ts-ignore
            this.message = updatedToken;
            localStorage.setItem('tokenServer', this.message.data.token);
            localStorage.setItem('statusServer', String(this.message.code));
            if (localStorage.getItem('statusServer') !== '0') {
              localStorage.removeItem('statusServer');
              this.router.navigate(['/home']);
            } else {
              localStorage.removeItem('statusServer');
              const str = userData.email;
              if (str.slice(-19, str.length) === 'ntq-solution.com.vn') {
                localStorage.setItem('data', JSON.stringify(userData));
                this.router.navigate(['/user']);
                console.log(' sign in data : ', userData);
                this.username = userData.name;
                this.linkimage = userData.image;
                this.email = userData.email;
                this.isLoggedIn = true;
                this.click.emit(event.target);
              } else {
                this.notificationAlertService.open('error', 'Error', 'Your email is not allowed to log into the system');
              }
            }
          }
        );
      }
    );
  }

  public socialSignOut() {
    // @ts-ignore
    this.logoutService.sendMessageLogoutToServer().subscribe(
      (updatedToken) => updatedToken);
    let socialPlatformProvider;
    socialPlatformProvider = GoogleLoginProvider.PROVIDER_ID;
    // @ts-ignore
    this.socialAuthService.signOut(socialPlatformProvider).then();
    this.click.emit(event.target);
    localStorage.clear();
    localStorage.removeItem('lastAction');
    this.router.navigate(['/home']);
    window.location.reload();
  }

  // @ViewChild('mess') mess: TemplateRef<any>;
  ngOnInit() {
    const dataUserLogin = JSON.parse(localStorage.getItem('data'));
    if (dataUserLogin != null) {
      this.username = dataUserLogin.name;
      this.linkimage = dataUserLogin.image;
      this.isLoggedIn = true;
    }
  }
}
