import { Injectable } from '@angular/core';
import {NotificationsService} from 'angular2-notifications';
import {FormBuilder} from '@angular/forms';

@Injectable({
  providedIn: 'root'
})
export class NotificationAlertService {
  tong: any;
  types = ['alert', 'error', 'info', 'warn', 'success'];
  animationTypes = ['fromRight', 'fromLeft', 'scale', 'rotate'];
  constructor(private servicee: NotificationsService,
              private fb: FormBuilder) {
  }
  // @ts-ignore
  open(isType, isTitle, isContent) {
    this.tong = this.fb.group({
      type: isType,
      title: isTitle,
      content: isContent,
      timeOut: 3000,
      showProgressBar: true,
      pauseOnHover: true,
      clickToClose: true,
      animate: 'fromRight'
    });
    const temp = this.tong.getRawValue();
    const type = temp.type;
    const title = temp.title;
    const content = temp.content;

    delete temp.title;
    delete temp.content;
    delete temp.type;
    this.servicee.create(title, content, type, temp);
  }
}
