import {Component, Input, OnInit} from '@angular/core';
import {MembersService} from '../../../../services/members.service';
import {ActivatedRoute} from '@angular/router';
import {Member} from '../../../../models/mock-member';

@Component({
  selector: 'app-members',
  templateUrl: './members.component.html',
  styleUrls: ['./members.component.styl']
})
export class MembersComponent implements OnInit {
  @Input() showCap;
  showNotMember = false;
  showListMember = false;
  members: Member[];
  constructor(
    private membersService: MembersService,
    private route: ActivatedRoute,
  ) { }
  getMembers(): void {
    const id = +this.route.snapshot.paramMap.get('id');
    this.membersService.getMembers(id).subscribe(
      (members) => {
        this.members = members;
        // @ts-ignore
        const isMember = members.filter(member => member.isCaptain === 'false') ;
        if (isMember.length < 1) {
          this.showNotMember = true;
          this.showListMember = false;
        } else if (isMember.length >= 1) {
          this.showNotMember = false;
          this.showListMember = true;
        }
      }
    );
  }

  ngOnInit() {
    this.getMembers();
  }

}
