import { Injectable } from '@angular/core';

import { Observable, of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {Message} from '../models/message';

// const httpOptions = {
//   headers: new HttpHeaders({'Content-Type': 'application/json'})
// };

@Injectable({
  providedIn: 'root'
})
// "Accept: application/json
// Accept-Charset: utf-8
// Content-Type: application/json;charset=utf-8
// Authorization: tokenGoogle"
export class LoginService {
  private tokenURL = 'http://192.168.4.42:8080/v1/sessions';
  // private tokenURL = 'http://localhost:3000/tokens';
  getTokenFromServer(): Observable<Message[]> {
    return this.http.get<Message[]>(this.tokenURL, {
      headers: new HttpHeaders(
      {
        Authorization: localStorage.getItem('googleToken'),
        ContentType: 'application/json',
        Accept: 'application/json'
      })}
      ).pipe(
      tap(receiveStatus => JSON.stringify(receiveStatus)),
      catchError(error => of([]))
    );
  }
  constructor(
    private http: HttpClient) { }
}
