import {Component, OnInit} from '@angular/core';
import { FormGroup } from '@angular/forms';
import { FormBuilder, FormControl } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { Validators } from '@angular/forms';
import { ContentsService } from '../../../../../services/contents.service';
import { Level } from '../../../../../models/level';

@Component({
  selector: 'app-popup-form-content',
  templateUrl: './popup-form-content.component.html',
  styleUrls: ['./popup-form-content.component.styl']
})
export class PopupFormContentComponent implements OnInit {

  public contentForm: FormGroup;
  constructor(private formBuilder: FormBuilder,
              private dialogRef: MatDialogRef<PopupFormContentComponent>,
              private contentsService: ContentsService) { }

  onNoClick(): void {
    this.dialogRef.close();
  }

  isFieldValid(field: string) {
    return !this.contentForm.get(field).valid && this.contentForm.get(field).touched;
  }

  displayFieldCss(field: string) {
    return {
      'has-error': this.isFieldValid(field),
      'has-feedback': this.isFieldValid(field)
    };
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach(field => {
      console.log(field);
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }
  ngOnInit() {
    this.contentForm = this.formBuilder.group({
      title: [ null, [Validators.required]],
      level: [ null, [Validators.required]],
      startDate: [ null, [Validators.required]],
      endDate: [ null, [Validators.required]],
      description: [ null , [Validators.required]],
    });
  }

  onSubmit() {
    // if (isNaN(this.data.ID)) {
    //   this.contentsService.addContact(this.contentForm.value);
    //   this.dialogRef.close();
    // } else {
    //   this.contentsService.editContact(this._contactForm.value);
    //   this.dialogRef.close();
    // }
  }

}
