import { Component, OnInit } from '@angular/core';
import {FollowingClass} from '../../models/following-class';
import {FollowingClassService} from '../../services/following-class.service';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.styl'],
})
export class MenuComponent implements OnInit {
  isLoggedIn;
  FollowingClasses: FollowingClass[];
  constructor( private followingClassService: FollowingClassService) { }
  getFollowingClassesFromServices(): void {
    this.followingClassService.getFollowingClass().subscribe(
      (updatedFollowingClasses) => {
        this.FollowingClasses = updatedFollowingClasses.sort((a, b) => {
          const nameA = a.name.toUpperCase();
          const nameB = b.name.toUpperCase();
          if (nameA < nameB) {
            return -1;
          }
          if (nameA > nameB) {
            return 1;
          }
          return 0;
        });
        console.log(this.FollowingClasses);
      }
    );
  }
  displayFollowingClass(event) {
    if (localStorage.getItem('data') != null) {
      this.isLoggedIn = true;
    } else {
      this.isLoggedIn = false;
    }
  }
  ngOnInit() {
    this.getFollowingClassesFromServices();
    this.displayFollowingClass(event);
  }

}
